
import { h, Component } from 'preact';
import style from './style';

import { Check, AlertCircle } from 'preact-feather';
import { Link } from 'preact-router/match';

let cms = []

for (let i = 1; i < 25; i++){
	cms.push(`CM${i}`);
}

let cos = []

for (let i = 1; i < 17; i++){
	cos.push(`CO${i}`);
}


const ELIGIBLEPOSTCODES = [...cms, ...cos, "SG", "SS"];

const checklitres = (litres="")=>{
	const n = Number(litres);
	
	if (isNaN(n))
		return false;
	return n >= 100 && n <= 2000;
}

const checkpostcode = (postcode="")=>{
	const p = postcode.replace(/\s/g,"").toUpperCase();
	
	return ELIGIBLEPOSTCODES.indexOf(p.toUpperCase()) !== -1;
}

export default class Home extends Component {
	
	state = {
		postcode: "",
		litres: "",
		screen: "home",
	};

	renderLitres(){
			const empty = this.state.litres.trim() === "";
			const valid = checklitres(this.state.litres);
			const icon = valid ?  <Check color={"white"} size={34}/> : <AlertCircle color={"white"} size={34}/>;

			return <div class={style.topcenter}>
						<div class={style.centerleft}>
							<div class={style.label}>how many litres?</div>
							<div class={style.smallabel}>minimum 100, maximum 2000</div>
						</div>
						<div class={style.centerright}>
							<input value={this.state.litres} onInput={e=>this.setState({litres:e.target.value})} placeholder="e.g. 250" class={style.input}></input>
							<div class={style.erroricon}>
								{!empty && icon}
							</div>
						</div>
					</div>
	
	}

	renderPostcode(){
			const empty = this.state.postcode.trim() === "";
			const valid = checkpostcode(this.state.postcode);
			const icon = valid ?  <Check color={"white"} size={34}/> : <AlertCircle color={"white"} size={34}/>;

			return <div class={style.bottomcenter}>
						<div class={style.centerleft}>
							<div class={style.label}>your postcode</div>
							<div class={style.smallabel}>we only need the first part e.g CO1</div>
						</div>
						<div class={style.centerright}>
							<input value={this.state.postcode} onInput={e=>this.setState({postcode:e.target.value})} placeholder="e.g. CO1" class={style.input}></input>
							<div class={style.erroricon}>
								{!empty && icon}
							</div>
						</div>
					</div>
	}

	renderQuote(){
		
		return <div class={style.rightbottom}>
		<div class={style.button}>
			<div class={style.buttontext}>
				<div onClick={e=>this.setState({screen:"quote"})}>get my quote</div>
			</div>
		</div>
	</div>
	}
	

	renderHomeContent(){
		return <div class={style.centerbox}>
			{this.renderLitres()}
			{this.renderPostcode()}
		</div>
	}

	renderQuoteContent(){
		return <div class={style.flipbox}>
			
		</div>
	}

	render(){
		const cansubmit = checklitres(this.state.litres.trim()) && checkpostcode(this.state.postcode.trim());

		return <div id={style.pricing}>
			<div class={style.containerbox}>
				<div class={style.imagebox}>
					<div class={style.dropimage}></div>
				</div>
				<div class={style.roundedtop}>
					<div class={style.lefttop}></div>
					<div class={style.righttop}>
						<div class={style.logo}></div>
					</div>
				</div>
				
				{this.state.screen==="home" && this.renderHomeContent()}
				{this.state.screen==="quote" && this.renderQuoteContent()}
				

				<div class={style.roundedbottom}>
					<div class={style.leftbottom}></div>
					{cansubmit && this.renderQuote()}
				</div>
			</div>
		</div>
	}

}





